REG.reg.registerSkin('wsp-app/c-workbench', 1, /* language=CSS */ ``);

customElements.define('wsp-app', WspApp);


class WorkbenchTools extends ActionMenu<WspApp> {

	constructor() {
		super("tools");
		this._label = "Volets";
		this._description = "Affichage des volets latÃ©raux";
		//this._icon = "/@skin@/wsp/apps/wspApp/views.svg"
	}

	getActions(ctx: WspApp): IAction<WspApp>[] {
		if (!this._actions) {
			this._actions = ctx.workbench.getAreaTools().map((a) => new ShowTool(a));
			this._actions.push(new ActionSeparator());
			this._actions.push(new ShowFlapTabs());
		}
		return this._actions as IAction<WspApp>[];
	}
}

class ShowTool extends ActionSignboardWrapper<WspApp> {
	constructor(toolArea: IArea<WspApp>) {
		super(toolArea.getId());
		this._wrapped = toolArea;
	}

	isToggle(ctx: WspApp): this is IActionToggle<WspApp> {return true}

	getDatas(api: "toggle", ctx: WspApp): any {return ctx.workbench.getTab(this._wrapped.getId())?.selected || false}

	execute(ctx: WspApp, ev?: Event): any | "noPreventDefault" {
		ctx.workbench.toggleAreaTool(this._wrapped.getId());
	}
}

class ShowFlapTabs extends Action<WspApp> implements IActionToggle<WspApp> {
	constructor() {
		super("showFlapTabs");
		this._label = "Afficher les onglets";
	}

	isToggle(ctx: WspApp): this is IActionToggle<WspApp> {return true}

	getDatas(api: "toggle", ctx: WspApp): any {
		return !ctx.workbench.classList.contains("hideFlapTabs");
	}

	execute(ctx: WspApp, ev?: Event): any | "noPreventDefault" {
		const showFlapTabs = !ctx.workbench.classList.toggle("hideFlapTabs");
		const persistUS = ctx.reg.getPersistUserStates();
		if (persistUS) persistUS.setUserDatas({showFlapTabs});
	}
}


class OpenWspDocApp extends Action<WspApp> {
	constructor() {
		super('openWspDoc');
		this._label = "Mode plan";
		this._icon = "/@skin@/wsp/views/toc/toc.svg";
		this._group = "other";
		this._visPerms = "ui.wspDocApp";
	}

	isVisible(ctx: WspApp): boolean {
		//XXX Ã  remplacer en 5.1 par un deny sur role ~default de la perm ui.wspDocApp avec levelAuth max ?
		if (!ctx.reg.getPref<boolean>("wspDocApp.enabled", false)) return false;
		return super.isVisible(ctx);
	}

	execute(ctx: WspApp, ev?: Event): any | "noPreventDefault" {
		const curr = new InfoReqCurrentItem();
		ctx.infoBroker.dispatchInfo(curr, ctx);
		if (curr.shortDesc && ctx.reg.env.wsp.wspMetaUi.getItemType(curr.shortDesc.itModel).datas.asDoc) {
			(desk as AppFrameDeskFeat).findAndOpenApp({u: ctx.universe.getId(), wspDoc: ctx.wsp.code, doc: SRC.srcRef(curr.shortDesc)} as JWspDocAppDef, ev);
		} else {
			(desk as AppFrameDeskFeat).findAndOpenApp({u: ctx.universe.getId(), wspDoc: ctx.wsp.code} as JWspDocAppDef, ev);
		}
	}
}

